/* @import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@300&display=swap"); */
body {
  margin: 0;
  padding: 0;
}

.header3 {
  height: 44.43px;
  width: 372.46px;
  color: #333;
  font-family: "Montserrat", sans-serif;
  font-size: 24px;
  font-weight: 300;
  line-height: 24px;
  margin: auto;
  margin-top: 110px;
  text-align: center;
}

.company-logo {
  max-width: 150px;
  max-height: 80px;
  margin-top: 50px;
}

.standing {
  align-items: center;
  height: 110px;
  width: 353px;
  color: #333;
  font-family: "Montserrat", sans-serif;
  font-size: 35px;
  font-weight: 300;
  letter-spacing: -0.16px;
  line-height: 42px;
  margin-top: 25%;
  margin-left: 45%;
}

.ballons {
  height: 402px;
  width: 625px;
  margin-top: 40px;
}

.standing2 {
  height: 444px;
  background-color: #f6f6f6;
  margin-top: 100px;
}

.marketing {
  margin-top: 50px;
  justify-content: center;
}

.marketing .row {
