}

.megaphone {
  margin-top: 50px;
  height: 124.44px;
  width: 124.41px;
}

.mtext {
  height: 110px;
  width: 450px;
  color: #333;
  font-family: "Montserrat", sans-serif;
  font-size: 40px;
  font-weight: 300;
  letter-spacing: -0.18px;
  line-height: 58px;
  text-align: center;
  margin-top: 70px;
}

.carousel-container {
  margin: auto;
  margin-top: 120px;
  margin-bottom: 20px;
}

.services {
  justify-content: center;
}

.buildingText {
  height: 86px;
  color: #333;
  font-size: 58px;
  font-weight: 400;
  letter-spacing: -0.26px;
  line-height: 58px;
  text-align: center;
}

.engineO {
  margin-top: 80px;
  margin-left: 130px;
}

.herosheContainer {
