  width: 100%;
  background-color: rgba(242, 242, 242, 0.4);
  margin-top: 50px;
  margin-bottom: 70px;
}

.casestudies {
  height: 14px;
  width: 101px;
  color: #333;
  font-size: 14px;
  font-weight: 500;
  letter-spacing: -0.42px;
  line-height: 14px;
  padding-top: 30px;
  padding-left: 15px;
  text-transform: uppercase;
}

.herosheText1 {
  height: 114px;
  width: 450px;
  color: #333;
  font-size: 39px;
  font-weight: 300;
  letter-spacing: -0.18px;
  line-height: 41px;
}
.partcontainer {
  height: 128px;
  width: 434px;
  color: #333;
  font-size: 23.5px;
  line-height: 32px;
}

.herosheText2 {
  font-size: 23.5px;
  font-weight: 100;
  line-height: 32px;
}

.herosheText3 {
  height: 20px;
  width: 86px;
  color: #39f;
  font-size: 20px;
  font-weight: 500;
  line-height: 20px;
}

.herosheText4 {
  height: 64px;
}

.herosheImage {
  height: 572px;
  width: 471px;
  border-radius: 5px;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.3);
}

.herosheContainer2 {
  padding-top: 100px;
}

.partners-wrapper {
  display: flex;
  align-items: center;
  justify-items: center;
}

.partner-images {
  margin: auto;
  display: flex;
  justify-content: center;
  align-items: center;
}

.heroshecontainer2 {
  margin-top: 100px;
}

.herosheText4 {
  font-size: 60px;
}

.see-all-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 140px;
  margin-bottom: 144.74px;
}

.see-all-wrapper img {
  height: 35.26px;
  width: 35.26px;
  margin-top: 5px;
}

.see-all {
  height: 30px;
  color: #39f;
  font-family: Gotham;
  font-size: 30px;
  font-weight: 600;
  letter-spacing: 0.1px;
  line-height: 30px;
  text-align: center;
}

.see-all:hover,
.see-all:focus {
  text-decoration: none;
}

.standing-mb {
  display: none;
}

.partners-mb {
  display: none;
}

.services-header {
  display: block;
  height: 12px;
  color: #000;
  font-family: Gotham;
  font-size: 12px;
  font-weight: 300;
  letter-spacing: 3.43px;
  line-height: 12px;
  text-align: center;
}

.herosheImage-mb {
  display: none;
}

.see-how-wrapper {
  display: none;
}

